import React, { useState, useEffect } from "react";
import moment from 'moment'
import {
    CButton,
    CCol,
    CModal,
    CModalBody,
    CModalFooter,
    CModalHeader,
    CModalTitle,
    CRow,
} from "@coreui/react";
import "Component/Modal/ModalConfirmation/index.scss";
const PopUpUnfinished = ({ moduleEntry, redirect, previousPage, discard,setDiscard,moduleFirstLoad }) => {
    const dimention = document.documentElement.clientHeight
    const height = dimention - 900
    const show = discard || !moduleEntry ? false : true

    if (!moduleFirstLoad) return (
        <CModal
            show={true}
            size="lg"
            closeOnBackdrop={false}
        >
            <div className="green h4 w-100 text-center p-4">
                Checking Unfinished Entry
            </div>
        </CModal>
    )
    return (
        <CRow className={show ? null : 'd-none'}>
            <CCol>
                <CModal
                    show={show}
                    size="lg"
                    closeOnBackdrop={false}
                >

                    <div className="d-flex flex-row-reverse pr-3 pt-3 mt-1">
                        <i onClick={() => previousPage()} className='iconU-modalClose' ></i>
                    </div>

                    <CModalHeader className="px-5 pt-3 border-none">
                        <CModalTitle className="green LatoBold respon-txt">Unfinished Entry Left! <span><i className="em em-speak_no_evil"></i></span></CModalTitle>
                    </CModalHeader>
                    <CModalBody className="px-5 py-0" style={{ minHeight: height }}>
                        <p className="text mb-3 fontSizeWeightNumber">
                            You must complete the Entry that you worked on :
                        </p>
                        <div style={{ backgroundColor: '#F5F7FA' }} className='w-100  rounded p-3 d-flex justify-content-between align-items-center'>
                            <div>
                                <div className="respon-txt">{moment(moduleEntry?.jobPost?.createdAt).format("dddd, MMMM DD YYYY, h:mm:ss a")}</div>
                                <div className="green respon-txt">
                                    {`${moduleEntry?.jobPost?.areaName} - ${moduleEntry?.jobPost?.list.length} SKU${moduleEntry?.jobPost?.list.length > 1 ? '(s)' : ''}`}
                                </div>
                            </div>
                            <div onClick={() => setDiscard()} className='iconU-entryTrash emptyTextField' style={{opacity:0.5, fontSize:'1.2rem'}}/>
                        </div>
                        <p className="text mt-3 fontSizeWeightNumber">
                            Do you want to continue this Entries ?
                        </p>
                        <br />
                    </CModalBody>

                    <CModalFooter className="px-5 pb-5 pt-0 justify-content-start border-none">
                        <CButton onClick={() => previousPage()} className="bg-background text no-shadow px-5 mb-0 mx-0">
                            Later
                        </CButton>
                        <CButton onClick={() => redirect()} className="bg-green no-shadow px-5 mb-0 ml-3 mr-0">
                            Continue
                        </CButton>{" "}
                    </CModalFooter>
                </CModal>
            </CCol>
        </CRow>
    );
};

export default PopUpUnfinished
