import React, { useEffect } from 'react'
import Img from 'assets/icons/sayur-box-logo.svg'
import { isFloat } from 'services'
import { CProgress } from '@coreui/react'

const SKUItem = ({
    selectAll,
    img = Img,
    skuDescription = 'SKU Description',
    skuNumber = '1001',
    uomLabel = 'weight',
    uomValue = 100.3,
    uom = 'KG',
    isCheckedHandler = null,
    check = false,
    idx = 0,
    singleView,
    setCreatePo,
    target,
    targetMovementProgress
}) => {
    const dimention = document.documentElement.clientWidth
    const mobile = dimention <= 500 ? true : false
    const [isChecked, setIsChecked] = React.useState(check)
    const checkHandler = () => {
        setIsChecked(!isChecked)
        if (isCheckedHandler) isCheckedHandler(!isChecked)
    }

    const createPoSingleView = () => {
        setIsChecked(true)
        isCheckedHandler(true)
        if (typeof (setCreatePo) === 'function') setCreatePo({ show: true, skuNumber })
    }

    const showProgress = target ? true : 'd-none'

    const totalMove = targetMovementProgress?.length ? targetMovementProgress[0].totalMove : 0
    const totalUos = targetMovementProgress?.length ? targetMovementProgress[0].totalUos : 0

    useEffect(_ => {
        if (selectAll) {
            setIsChecked(true)
            isCheckedHandler(true)
        }
    }, [selectAll])

    return (
        <div onKeyDown={(e) => e.keyCode === 13 ? createPoSingleView() : false} tabindex="0" key={idx} className={`w-100 border d-md-flex sku-item justify-content-between ${mobile ? 'pl-0 pr-0' : 'pl-2 pr-3'} py-2 mb-3 rounded ${isChecked ? 'bg-softGreen' : null}`}>
            <div className={`d-flex align-items-center w-100 cursor-pointer ${mobile ? 'pl-2 pr-3' : 'pl-0 pr-0'}`} onClick={(checkHandler)}>
                <div
                    style={{ cursor: 'pointer', height: '23px', width: '23px', minWidth: '23px' }}
                    className={`mr-3 text-white d-flex align-items-center rounded px-1 ${isChecked ? 'iconU-pickCheck bg-green pl-1-5' : 'bg-borderHeader'}`} />
                <img
                    src={img} alt='sku image'
                    className='w-5 mr-3 border softGreen rounded' />
                <div className='text-left'>
                    <div className='mr-3 text LatoBold fontSizeTitle'>{skuDescription}</div>
                    <div className='text fontSizeTitle'>{skuNumber}</div>
                </div>
            </div>
            <hr className={`${mobile ? null : 'd-none'}`} />
            <div className={`d-flex w-100 ${mobile ? 'pr-3' : 'pr-0'} pl-5 pl-md-0 align-items-center`}>
                <div className={`d-flex justify-content-md-end align-items-center w-100 ${mobile ? 'justify-content-end' : null}`}>
                    <div className={`w-25 mr-4 ${showProgress} tooltips`}>
                        <span style={{ marginLeft: -260 }} className={`${'tooltiptext'}`}>
                            <span className='pl-1 pr-1'>Movement Percentage</span>
                        </span>
                        <div className='latoBold'>{`${totalMove}/${totalUos}`}<span className='font-green'>{` (${isFloat(totalMove / totalUos * 100)}%)`}</span></div>
                        <CProgress value={totalMove} max={totalUos} className="mb-3" />
                    </div>
                    <div className={`mr-3 iconU-sideBarDot d-flex align-items-center ${showProgress}`} />
                    <div className={`my-0 mr-3 text fontSizeWeightNumber ${mobile ? 'd-none' : null}`}>{`Total ${uom.toUpperCase() === 'KG' ? uomLabel.toUpperCase() : uom.toUpperCase()}`}</div>
                    <div className='mr-1 font-weight-bold green fontSizeWeightNumber'>{isFloat(uomValue)}</div>
                    <div className='mr-3 font-weight-bold green fontSizeWeightNumber'>{uom}</div>
                </div>
                <div onClick={(createPoSingleView)} style={{ paddingLeft: '10px', paddingRight: '10px', paddingTop: '10px', paddingBottom: '8px' }} className={`${singleView ? null : 'd-none'} cursor-pointer iconU-sideBarHideRight font-weight-bold green d-flex align-items-center border rounded border-green`}></div>
            </div>
        </div>
    )
}

export default SKUItem